.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}













.navbar {
  background-color: black;
  padding: 8px;
  border-bottom: solid rgba(0, 123, 255, 0.3) 1px;
}

.nav-list {
  list-style-type: none;
  margin: 0;
  padding: 10px;
  overflow: hidden;
}

.nav-item {
  display: inline;
  margin-right: 10px;
}

.nav-item a {
  text-decoration: none;
  color: white;
  font-weight: bold;
  padding: 8px;
  border-radius: 16px;
  transition: background-color 0.3s;
}

.nav-item a:hover {
  background-color: #007bff;
}

.nav-item.active a {
  background-color: #007bff;
}